@use "sass:math";

// インナー幅設定
// コンテンツのインナー幅（最も共通の幅が多い部分）
$inner: 1080px;
// ※キュンパス幅ではなくインナーの幅を指す

// 余白
$padding-pc: 25px;
$padding-sp: 15px;

////////////////////
// フォント設定
////////////////////

$japan-font-family: "Noto Serif JP", sans-serif;
$english-font-family: "Crimson Text", sans-serif;
// ※検証ツールでしっかり反映されているか確認する

body {
    font-family: $japan-font-family;
    color: #2c3e50;
}

// フォントウェイト
$regular: 400;
$medium: 500;
$bold: 700;

////////////////////
// 色の指定
////////////////////

//色指定（共通性のあるものは出来る限り指定すると良い）
$blue: #457b9d;
$skyblue: #a8dadc;
$yellow: #ffe8a1;
$white: #fff;
$text: #2c3e50;
