@use "global" as *;
.company-gallery {
    position: relative;
}
.company-gallery__images {
    display: grid;
    gap: rem(8);
    margin-top: rem(40);
    @include mq(md) {
        grid-auto-flow: dense;
        grid-template-columns: repeat(11, 1fr);
        margin-top: rem(80);
    }
}

.company-gallery__image img {
    height: 100%;
    object-fit: cover;
}

.company-gallery__image:nth-of-type(6n + 1),
.company-gallery__image:nth-of-type(6n + 6) {
    grid-row: span 2;
    aspect-ratio: 345 / 523;
    overflow: hidden;
    max-width: 100%;
    height: 100%;
    @include mq(md) {
        grid-column: span 5;
        aspect-ratio: 492 / 746;
    }
}

.company-gallery__image:nth-of-type(6n + 2),
.company-gallery__image:nth-of-type(6n + 3),
.company-gallery__image:nth-of-type(6n + 4),
.company-gallery__image:nth-of-type(6n + 5) {
    grid-row: span 1;
    aspect-ratio: 345 / 219;
    @include mq(md) {
        grid-column: span 6;
        aspect-ratio: 580 / 369;
    }
}
/* モーダル全体のスタイル */
.company-gallery__modal {
    display: none; /* デフォルトでは非表示 */
    position: fixed;
    z-index: 1000;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    overflow: auto;
    background-color: rgba(0, 0, 0, 0.8); /* 半透明の背景 */
    justify-content: center;
    align-items: center;
}
/* モーダル内のコンテンツ */
.company-gallery__content {
    position: relative;
    width: 90%;
    height: 90%;
    display: flex;
    align-items: center;
    justify-content: center;
}

/* 画像のスタイル */
.company-gallery__content img {
    max-height: 100%;
    width: auto;
    object-fit: contain;
}

/* モーダルの閉じるボタン */
.company-gallery__close {
    position: absolute;
    top: 20px;
    right: 35px;
    color: $white;
    font-size: 40px;
    font-weight: bold;
    cursor: pointer;
    transition: all 0.3s;
}

.company-gallery__close:hover {
    color: $blue;
}
