.to-top {
  position: fixed;
  right: 0.9375rem;
  z-index: 88;
}
@media screen and (min-width: 768px) {
  .to-top {
    right: 1.25rem;
  }
}

.to-top a {
  display: inline-block;
  width: 3rem;
  height: 3rem;
  border-radius: 50%;
  background-color: #fff;
  transform: rotate(-90deg);
  position: relative;
  border: 1px solid #457b9d;
}
@media screen and (min-width: 768px) {
  .to-top a {
    width: 5rem;
    height: 5rem;
    box-shadow: -4px 0 4px 0 rgba(0, 0, 0, 0.25);
  }
}

.to-top a::before {
  display: inline-block;
  content: "";
  width: 1.75rem;
  height: 0.375rem;
  border-bottom: 1px solid #457b9d;
  border-right: 1px solid #457b9d;
  transform: skew(45deg);
  top: 1.125rem;
  left: 0.5rem;
  position: absolute;
}
@media screen and (min-width: 768px) {
  .to-top a::before {
    width: 2.5rem;
    top: 2.125rem;
    left: 1rem;
  }
}

.to-top.UpMove {
  animation: UpAnime 0.5s forwards;
}

@keyframes UpAnime {
  from {
    opacity: 0;
    transform: translateY(100px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}
.to-top.DownMove {
  animation: DownAnime 0.5s forwards;
}

@keyframes DownAnime {
  from {
    opacity: 1;
    transform: translateY(0);
  }
  to {
    opacity: 1;
    transform: translateY(100px);
  }
}
.top {
