@use "global" as *;
.column-card {
    width: 100%;
    max-width: rem(600);
    box-shadow: 2px 2px 10px 0px rgba(0, 0, 0, 0.25);
    padding: rem(16) rem(16) rem(24);
    background-color: $white;
    transition: all 0.5s;
}

.column-card__body {
    margin-top: rem(24);
}

.column-card__image {
    aspect-ratio: 301 / 201;
    overflow: hidden;
}

.column-card__image img {
    width: 100%;
    height: 100%;
    object-fit: cover;
    transition: all 0.3s;
}

.column-card__date {
    font-family: $english-font-family;
    font-size: rem(12);
    line-height: 1.5;
    display: block;
}

.column-card__title {
    font-size: rem(16);
    font-weight: $medium;
    line-height: 1.5;
    padding-bottom: rem(4);
    border-bottom: 1px solid $blue;
}

.column-card__text {
    margin-top: rem(16);
    line-height: 1.5;
}

.column-card:hover {
    opacity: 0.9;
}
